
.c-wrapper-posr{position: relative;
}
.search {
    padding: 8px 65px 8px 8px;
    background-color: #e0e4ec;
    position: relative;
}
.search-reset{
    padding-right: 8px;
}
.search,.city-panel{
    -webkit-user-select: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-drag: none;
}
.search input {
    display: block;
    width: 100%;
    height: 40px;
    padding-left: 36px;
    border-radius: 5px;
    border: 0;
    box-sizing: border-box;
    font-size: 14px;
    border:1px solid #dcdcdc;
}
.search .clear {
    position: absolute;
    right: 65px;
    top: 8px;
    height: 20px;
    padding: 10px 10px;
}
.search-reset .clear {
    right: 8px;
    display: none;
}
.search .clear i {
    display: block;
    width: 21px;
    height: 21px;
    background: #ccc;
    border-radius: 50%;
    line-height: 20px;
    font-size: 20px;
    color: #fff;
    text-align: center;
    font-style: normal;
}
.search .cancel {
    position: absolute;
    width: 65px;
    height: 40px;
    top: 8px;
    right: 0;
    text-align: center;
    font-size: 18px;
    line-height: 40px;
}
.search-reset .cancel{
    display: none;
}
.search .search-icon{
    position: absolute;
    left: 18px;
    top: 20px;
    width: 12px;
    height: 12px;
    border: 1px solid #999;
    border-radius: 50%;
}
.search .search-icon:after{
    content: '';
    position: absolute;
    width: 1px;
    height: 8px;
    left: 13px;
    top: 10px;
    background: #999;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.search-complete{
    display: none;
}
.search-complete .autofill-wrap{
    border:none;
}
.search-complete table tr{
    height:44px;
    line-height:44px;
    font-size:14px;
}
.search-complete table tr div{
    font-family:微软雅黑;
    font-size:14px;
}
.city-notice{
    background: #e9ecf1;
}
.city-notice.none{display: none;
}
.city-tab{
    background: #e9ecf1;
    border-width: 1px 0 1px 0;
    border-image: url('./border-dc.png') 2 0 stretch;
    line-height: 40px;
    color: #666;
    font-size: 15px;
}
.city-tab li{
    float:left;
    width: 50%;
    text-align: center;
    box-sizing: border-box;
    padding-left: 15%;
}
.city-tab li.active{color: #50b400;position: relative;
}
.city-tab li:last-child{padding-left: 0;padding-right: 15%;
}
.city-tab li.active:after{
    content: '';
    position: absolute;
    left: 65%;
    bottom: 0px;
    width: 100px;
    height: 2px;
    background: #50b400;
    margin-left: -50px;
}
.city-tab li:last-child:after{padding-left: 0;left: 35%;
}
.city-wrapper {
    /*border-top: 1px solid #dcdcdc;*/
    position: relative;
}
.c-wrapper{
    padding-top: 8px;
    color: #666;
        position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
}
.c-wrapper.none{display: none;
}
.c-wrapper h3 {
    height: 30px;
    padding-left: 20px;
    line-height: 30px;
    /*background: #f0f0f0;*/
    /*border-bottom: 1px solid #dcdcdc;*/
    color: #777;
    font-weight: normal;
}
.c-wrapper h3.city-key-dw, .c-wrapper h3.city-key-ls, .c-wrapper h3.city-key-rm{line-height: 33px;height: 33px;font-size: 12px;
}
.c-wrapper .city-key:after {
    content: '';
    float: right;
    width: 11px;
    height: 11px;
    border-left: 1px solid #bbb;
    border-top: 1px solid #bbb;
    margin: 12px 16px 0 0;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
    transition: transform .2s linear;
    -webkit-transform-origin: 3px 3px;
    transform-origin: 3px 3px;
    transition: -webkit-transform .2s linear;
}
.c-wrapper .city-key.open {
    color: #50b400;
}
.c-wrapper .city-key.open:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.c-wrapper li {
    /*border-bottom: 1px solid #dcdcdc;*/
    height: 44px;
    padding: 0 20px;
    line-height: 44px;
    position: relative;
    background: #fff;
}
.c-wrapper .sort-city,.c-wrapper .require-key{
    border-width: 1px 0 1px 0;
    border-image: url('./border-dc.png') 2 0 stretch;
}
.c-wrapper .require-key{border-width: 0 0 1px 0;
}
.c-wrapper .sort-city li,.c-wrapper .require-city li{position: relative;overflow: hidden;
}
.c-wrapper .sort-city li:before,.c-wrapper .require-city li:before{position: absolute;content: '';left: 20px;bottom: 0;height: 1px;width: 100%;
    background-image: linear-gradient(to bottom,transparent 50%,#ddd 50%);
}
.c-wrapper .sort-city li:last-child:before,.c-wrapper .require-city li:last-child:before{display: none;
}
.c-wrapper li.select{
    color: #50b400;
    position: relative;
}
.c-wrapper li.select:after {
    content: "";
    position: absolute;
    width: 17px;
    height: 9px;
    right: 45px;
    top: 12px;
    border-left: 4px solid #50b400;
    border-bottom: 4px solid #50b400;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.c-wrapper .history-city-ul li.select:after, .c-wrapper .hot-city-ul li.select:after{display: none;
}
.c-wrapper .dw-city{padding: 0 35px 5px 15px;
}
.c-wrapper .dw-city li{
    background: #fff;
    border:1px solid #ddd;
    color: #666;
    font-size: 16px;
    border-radius: 3px;
}
.c-wrapper .dw-city li span{
    color: #999;
    font-size: 12px;
}
.c-wrapper .history-city-ul,  .c-wrapper .hot-city-ul {
    /*padding: 0 25px 0 5px;*/
    padding: 0 0 0 5px;
}
.c-wrapper .history-city-ul li, .c-wrapper .hot-city-ul li{
    padding: 0;
    margin:0 0 10px 10px;
    background: #fff;
    border: 1px solid #dcdcdc;
    border-radius: 3px;
    padding: 0 10px;
    min-width: 81px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    float: left;
    box-sizing: border-box;
}
.c-wrapper .block-city li {
    margin: 5px 20px 5px 15px;
    text-align: left;
    border: 1px solid #dcdcdc;
    height: 32px;
    line-height: 32px;
}
.c-wrapper .closed {
    display: none;
}
.city-nav {
    position: fixed;
    right: 0;
    top: 62px;
    font-size: 12px;
    color: #50b400;
    background:rgba(242, 244, 247,0.7);
    padding-top: 5px;
     -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-transform: translateZ(0);
    -webkit-user-select: none;
    -webkit-user-drag: none;
}
.city-nav.nav-fixed-top{bottom:0;
}
.city-nav.none,.city-nav.hidden{display: none;
}
.city-nav li {
    line-height: 20px;
    /*padding: 0 8px;*/
    padding: 0 2px;
    text-align: center;
    position: relative;
    font-size: 16px;
}
.city-nav .nav-city-dw, .city-nav .nav-city-ls, .city-nav .nav-city-rm {font-size: 14px;
}
.city-nav li span{
    position: absolute;
    right: 100%;
    top: 0;
    background: rgba(80, 180, 0,0.7);
    line-height: 20px;
    font-size: 18px;
    color: #fff;
    border-radius: 2px;
    display: block;
    text-align: center;
    white-space: nowrap;
    padding: 3px 6px;
    display: none;
}
.city-nav .nav-active span{display: block;
}
.city-nav li span:after{
    content: '';
    right: -7px;
    top: 50%;
    margin-top: -4px;
    border-color: transparent transparent transparent rgba(80, 180, 0,0.7); 
    border-style: dashed dashed dashed solid; 
    border-width: 4px;
    position: absolute;
}
.search-complete .autofill-wrap{
    position: relative;
}
@media screen and (width:375px){
.c-wrapper .hot-city-ul{
        padding: 0 0 0 11px;
}
.c-wrapper .hot-city-ul li{
        margin: 0 0 10px 4px;
}
}



