
.autofill-wrap{position:absolute;background-color:#FFF;font:12px/24px SimSun;width:100%;border:1px solid #ccc
}
.autofill-close{position:absolute;width:11px;height:11px;top:7px;right:10px;background:url(//img1.40017.cn/touch/public/autoComplete/pic.png?v=2) 0 -16px repeat scroll;cursor:pointer
}
.autofill-hd-inner{color:#999;font-size:12px;padding:0 10px;display:block
}
.autofill-tray{background:#fff;width:100%
}
.autofill-tray th{padding:0 10px
}
.autofill-tray th{border-bottom:1px solid #ddd
}
.autofill-tray th,.autofill-tray td,.autofill-hd-inner{white-space:nowrap;overflow:hidden
}
.autofill-tray td{color:#333
}
.autofill-tray .ltd{text-align:left
}
.autofill-tray .rtd{text-align:right
}
.autofill-hover{background-color:#e2e2e2
}
.autofill-hili{color:#f60;cursor:pointer
}
.autofill_item{line-height:30px;border-bottom:1px solid #ccc;padding-left:15px;display:block
}
.item-state-hover td{background-color:#e2e2e2
}
.show-title{border-bottom:1px solid #ccc
}





